import React, { useState, useEffect } from 'react';
import { View, Text, TextInput, Button, FlatList, TouchableOpacity, Alert } from 'react-native';
import { auth, db } from './firebaseConfig';
import { 
  signInWithEmailAndPassword, 
  createUserWithEmailAndPassword, 
  onAuthStateChanged, 
  signOut 
} from 'firebase/auth';
import { 
  collection, 
  doc, 
  setDoc, 
  getDoc, 
  onSnapshot, 
  query, 
  orderBy, 
  getDocs, 
  addDoc 
} from 'firebase/firestore';

const App = () => {
  const [user, setUser] = useState(null);
  const [users, setUsers] = useState([]);
  const [selectedChatUser, setSelectedChatUser] = useState(null);
  const [message, setMessage] = useState('');
  const [messages, setMessages] = useState([]);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
      if (currentUser) {
        loadUsers();
      }
    });

    return unsubscribe;
  }, []);

  useEffect(() => {
    if (Notification.permission !== "granted") {
      Notification.requestPermission().then(permission => {
        if (permission === "granted") {
          console.log("Notification permission granted.");
        }
      });
    }
  }, []);

  const loadUsers = async () => {
    const usersSnapshot = await getDocs(collection(db, 'users'));
    const usersList = usersSnapshot.docs
      .map((doc) => ({
        uid: doc.id,
        ...doc.data(),
      }))
      .filter((u) => u.uid !== user?.uid);
    setUsers(usersList);
  };

  const handleLogin = async () => {
    try {
      await signInWithEmailAndPassword(auth, email, password);
    } catch (error) {
      Alert.alert('Login error', error.message);
    }
  };

  const handleSignUp = async () => {
    try {
      const userCredential = await createUserWithEmailAndPassword(
        auth,
        email,
        password
      );
      const newUser = userCredential.user;
      await setDoc(doc(db, 'users', newUser.uid), {
        email: newUser.email,
        approved: false,
      });
      Alert.alert(
        'Registration Successful',
        'Your account has been created. Please wait for approval.'
      );
    } catch (error) {
      Alert.alert('Sign up error', error.message);
    }
  };

  const createChatRoom = async (selectedUser) => {
    const chatRoomId =
      user?.uid > selectedUser.uid
        ? `${user.uid}_${selectedUser.uid}`
        : `${selectedUser.uid}_${user?.uid}`;

    setSelectedChatUser(selectedUser);

    const q = query(
      collection(db, 'chatRooms', chatRoomId, 'messages'),
      orderBy('createdAt', 'desc')
    );

    onSnapshot(q, (querySnapshot) => {
      const messagesList = querySnapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));
      setMessages(messagesList);

      if (querySnapshot.docChanges().length > 0) {
        const lastMessage = querySnapshot.docChanges()[0].doc.data().text;
        const sender = querySnapshot.docChanges()[0].doc.data().user;

        // Web'de Bildirim Göster
        if (Notification.permission === "granted") {
          new Notification("Yeni Mesaj", {
            body: `${sender}: ${lastMessage}`,
          });
        }
      }
    });
  };

  const handleSendMessage = async () => {
    if (message.length > 0 && selectedChatUser) {
      const chatRoomId =
        user?.uid > selectedChatUser.uid
          ? `${user.uid}_${selectedChatUser.uid}`
          : `${selectedChatUser.uid}_${user?.uid}`;

      await addDoc(collection(db, 'chatRooms', chatRoomId, 'messages'), {
        text: message,
        createdAt: new Date(),
        user: user?.email,
      });
      setMessage('');
    }
  };

  if (!user) {
    return (
      <View style={{ padding: 20 }}>
        <Text>Email:</Text>
        <TextInput
          value={email}
          onChangeText={setEmail}
          style={{ borderWidth: 1, marginBottom: 10, padding: 5 }}
        />
        <Text>Password:</Text>
        <TextInput
          value={password}
          onChangeText={setPassword}
          secureTextEntry
          style={{ borderWidth: 1, marginBottom: 10, padding: 5 }}
        />
        <Button title="Login" onPress={handleLogin} />
        <Button title="Sign Up" onPress={handleSignUp} />
      </View>
    );
  }

  if (!selectedChatUser) {
    return (
      <View style={{ flex: 1, padding: 20 }}>
        <Text>Users</Text>
        <FlatList
          data={users}
          keyExtractor={(item) => item.uid}
          renderItem={({ item }) => (
            <TouchableOpacity onPress={() => createChatRoom(item)}>
              <View style={{ padding: 10, backgroundColor: '#f1f1f1', marginVertical: 5 }}>
                <Text>{item.email}</Text>
              </View>
            </TouchableOpacity>
          )}
        />
        <Button title="Logout" onPress={() => signOut(auth)} />
      </View>
    );
  }

  return (
    <View style={{ flex: 1, padding: 20 }}>
      <Text>Chatting with {selectedChatUser?.email}</Text>
      <FlatList
        data={messages}
        keyExtractor={(item) => item.id}
        renderItem={({ item }) => (
          <View style={{ padding: 10, backgroundColor: '#f1f1f1', marginVertical: 5 }}>
            <Text>{item.user}: {item.text}</Text>
          </View>
        )}
        inverted
      />
      <TextInput
        value={message}
        onChangeText={setMessage}
        placeholder="Write a message"
        style={{ borderWidth: 1, padding: 10, marginBottom: 10 }}
      />
      <Button title="Send" onPress={handleSendMessage} />
      <Button title="Back" onPress={() => setSelectedChatUser(null)} />
    </View>
  );
};

export default App;